  -webkit-transform: scale(0.8);
  -moz-transition: -moz-transform 0.3s, opacity 0.3s;
  -moz-transform: scale(0.8);
  -ms-transform: scale(0.8);
  transition: transform 0.3s, opacity 0.3s;
  transform: scale(0.8);
  opacity: 0; }

